import React, { Component } from "react";
import classNames from "classnames";
import "./Axis.css";
const selectableHead = "name";
class Axis extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: null
    };
    this.onMouseEnter = this.onMouseEnter.bind(this);
    this.onMouseLeave = this.onMouseLeave.bind(this);
  }
  onMouseEnter(selected) {
    const { handleSelect } = this.props;
    handleSelect(selected);
    this.setState({ selected });
  }

  onMouseLeave(deselected) {
    const { handleSelect } = this.props;
    handleSelect(null);
    this.setState({ selected: null });
  }

  render() {
    const { anchor, scale, selectable, selected, list, x } = this.props;
    const domain = scale.domain();

    return (
      <g className="Axis" transform={`translate(${x},0)`}>
        {domain.map(d => {
          const tickClassName = classNames("Tick", {
            selectable,
            selected: selected && d === selected,
            deselected: selected && d !== selected
          });
          return (
            <g
              className={tickClassName}
              key={d}
              transform={`translate(0,${scale(d)})`}
              onMouseEnter={() => this.onMouseEnter(d)}
              onMouseLeave={() => this.onMouseLeave(d)}
            >
              <a
                href={
                  selectable
                    ? `./projects/${
                        list.find(l => l[selectableHead] === d).path
                      }/`
                    : ""
                }
              >
                <text className="Tick-text" textAnchor={anchor} dy=".35em">
                  {d}
                </text>
                <text
                  className="Tick-text stroke"
                  textAnchor={anchor}
                  dx="-.1em"
                  dy=".2em"
                >
                  {d}
                </text>
              </a>
            </g>
          );
        })}
      </g>
    );
  }
}

export default Axis;



