import React, { Component } from "react";
import { scalePoint as d3ScalePoint } from "d3-scale";
import { csv as d3Csv } from "d3-fetch";
import Axis from "./Axis";
import Series from "./Series";
import { MARGIN, TARGET_HEADS } from "./config";
import "./App.css";
const PATH = "./data/list.csv";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: [],
      scaleX: null,
      scaleY: null,
      selected: null,
      width: 1000,
      height: 600
    }; //TODO: innerSize 계산
    this.handleSelect = this.handleSelect.bind(this);
  }
  handleSelect(selected) {
    this.setState({ selected });
  }
  componentDidMount() {
    const { width, height } = this.state;
    const innerW = width - MARGIN.left - MARGIN.right;
    const innerH = height - MARGIN.top - MARGIN.bottom;
    const scaleX = d3ScalePoint()
      .domain(TARGET_HEADS)
      .padding(0.1)
      .rangeRound([0, innerW]);

    d3Csv(PATH).then(list => {
      const scaleY = {};
      TARGET_HEADS.forEach(h => {
        if (!(h in scaleY)) scaleY[h] = [];
        list.forEach(l => {
          if (scaleY[h].indexOf(l[h]) < 0) scaleY[h].push(l[h]);
        });
        scaleY[h].sort((a, b) => a.localeCompare(b));
        scaleY[h] = d3ScalePoint()
          .domain(scaleY[h])
          .padding(0.1)
          .range([0, innerH]);
      });
      this.setState({ list, scaleX, scaleY });
    });
  }
  render() {
    const { list, width, height, scaleY, scaleX, selected } = this.state;
    let selectedRow = null;
    if (selected) {
      selectedRow = list.find(d => d.name === selected);
    }
    return (
      <div className="App">
        {scaleY &&
          scaleX &&
          list && (
            <div className="App-parcoords">
              <svg width={width} height={height}>
                <g transform={`translate(${MARGIN.left},${MARGIN.top})`}>
                  <g className="series-g">
                    {list.map(d => (
                      <Series
                        key={`${d.name}-${d.year}`}
                        data={d}
                        scaleX={scaleX}
                        scaleY={scaleY}
                        selected={
                          selected &&
                          (selected === d.name ? "selected" : "deselected")
                        }
                      />
                    ))}
                  </g>
                  <g className="axis-g">
                    {TARGET_HEADS.map((h, i) => (
                      <Axis
                        anchor={
                          i === 0
                            ? "end"
                            : i === TARGET_HEADS.length - 1
                              ? "start"
                              : "middle"
                        }
                        key={h}
                        head={h}
                        list={list}
                        scale={scaleY[h]}
                        selectable={h === "name"}
                        selected={selectedRow && selectedRow[h]}
                        x={scaleX(h)}
                        handleSelect={this.handleSelect}
                      />
                    ))}
                  </g>
                </g>
              </svg>
            </div>
          )}
        <div className="title top"><div>정보문화학 비주얼라이제이션 <span>2018</span></div></div>
        <div className="title bottom"><div><span>ISC Visualization</span> 2018</div></div>
      </div>
    );
  }
}

export default App;



