import React, { Component } from "react";
import { easeLinear as d3EaseLinear } from "d3-ease";
import { select as d3Select } from "d3-selection";
import "d3-transition";
import { line as d3Line, curveCatmullRom as d3CurveCatmullRom } from "d3-shape";
import classNames from "classnames";
import { TARGET_HEADS } from "./config";
import "./Series.css";

const startOffset = 2;
const lineGen = d3Line()
  .x(d => d.x)
  .y(d => d.y)
  .curve(d3CurveCatmullRom.alpha(1));

class Series extends Component {
  constructor(props) {
    super(props);
    this.seriesRef = React.createRef();
  }

  componentWillReceiveProps(nextProps) {
    if (this.seriesRef.current && nextProps.selected === "selected") {
      const { lineLength, textLength } = this;
      const lengthDiff = textLength - lineLength + 100;
      const seriesG = d3Select(this.seriesRef.current);
      const textPath = seriesG.select(".textPath > textPath");
      if (lengthDiff > 0) {
        textPath
          .attr("startOffset", startOffset)
          .transition()
          .delay(1600)
          .duration(lengthDiff * 24)
          .ease(d3EaseLinear)
          .attr("startOffset", -lengthDiff);
      }
    } else if (this.props.selected === 'selected' && !nextProps.selected) {
      const seriesG = d3Select(this.seriesRef.current);
      const textPath = seriesG.select(".textPath > textPath")
      textPath.interrupt().attr("startOffset", startOffset);
    }
  }

  componentDidMount() {
    const seriesG = d3Select(this.seriesRef.current);
    this.lineLength = seriesG
      .select(".linePath")
      .node()
      .getTotalLength();
    this.textLength = seriesG
      .select(".textPath.hidden")
      .node()
      .getBBox().width;
  }

  render() {
    const { data, scaleY, scaleX, selected } = this.props;
    const padding = 4;
    const seriesData = TARGET_HEADS.map((h, i, arr) => ({
      x:
        scaleX(h) +
        (i === 0 ? padding : i === arr.length - 1 ? -padding / 2 : 0),
      y: scaleY[h](data[h])
    }));
    const idName = `${data.name}-${data.year}`;
    const seriesClassName = classNames("Series", {
      selected: selected === "selected",
      deselected: selected === "deselected"
    });
    return (
      <g className={seriesClassName} ref={this.seriesRef}>
        <path id={idName} className="linePath" d={lineGen(seriesData)} />
        <text className="textPath">
          <textPath
            startOffset={startOffset}
            spacing="auto"
            href={`#${idName}`}
          >
            <tspan className="bold">{data.title}</tspan>
            <tspan dx=".35em">{data.desc}</tspan>
          </textPath>
        </text>
        <text className="textPath hidden">
          <tspan className="bold">{data.title}</tspan>
          <tspan dx=".35em">{data.desc}</tspan>
        </text>
      </g>
    );
  }
}

export default Series;



